.extra {
  margin-bottom: 87px;
}

.extra__wrapper {
  display: flex;
  gap: 80px;
}

.extra__title {
  font-size: 36px;
  font-weight: 400;
  line-height: 36px;
  color: #000000;
  margin-top: 0;
  margin-bottom: 80px;
}

.extra__list {
  width: 896px;
}

.extra__promotion {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 56px 42px 43px 42px;
  width: 161px;
  height: 189px;
  background-color: rgba(104, 183, 56, 0.85);
  position: relative;

  p {
    font-family: $font-lato;
    font-size: 16px;
    font-weight: 400;
    line-height: 20px;
    color: #444444;
    text-align: center;
    margin: 0;
  }

  &::before {
    content: "";
    display: block;
    width: 80px;
    height: 83px;
    background-image: url("../img/icon_gift.svg");
    margin-bottom: 46px;
  }

  &::after {
    content: "";
    display: block;
    width: 100%;
    height: 100%;
    background-image: url("../img/promotion-img.jpg");
    position: absolute;
    bottom: 0;
    z-index: -1;
  }
}
